<template>
  <div style="margin: 30px 0 0 0">
    <div class="gonggao bdb_d fix">
      <div class="txtScroll-top l fix zy_cur">
        <div class="l tit">
          <b class="dib vm">当前位置：</b>
        </div>
        <p class="l"><router-link to="/index">首页</router-link><span>公告信息</span></p>
      </div>
    </div>

    <div class="zy_contentbox">
      <div class="zy_content fix">
        <div class="zy_left_con">
          <div class="zy_twonav">
            <h2 class="tit">公告信息</h2>
            <ul>
              <li class="cur">
                <a href="javascript:void(0)"  @click="loadData(1)">公告列表</a>
              </li>
            </ul>
          </div>
        </div>

        <div class="zy_right_con">
          <div v-if="currpage == 'list'">
            <div class="zy_title fix">
              <h2>公告信息<i></i></h2>
            </div>
            <div class="news_list zy_listbox">
              <ul style="padding:0 30px 0 30px ;">
                <li v-for="(item, index) in list">
                  <a @click="toDetail(item.id)" href="javascript:void(0)">{{item.title}}</a>
                  <span>{{ item.publishDate }}</span>
                </li>
              </ul>
              <!-- 通用分页 -->
              <div class="fanye" style="text-align: left">
                <a href="#" :class="{customdisable: !page.hasPreviousPage}" @click="loadData(page.prePage)">上一页</a>
                <div v-for="(item, index) in page.navigatepageNums">
                  <a href="#" :class="{ current: item == page.pageNum }" @click="loadData(item)">{{ item }}</a>
                </div>
                <a href="#" :class="{ customdisable: !page.hasNextPage }" @click="loadData(page.nextPage)">下一页</a>
                <p><span>共{{ page.pages }}页</span></p>
              </div>
            </div>
          </div>
          <div v-if="currpage == 'detail'">
            <div class="zy_content fix">
              <div class="zy_conbox">
                <div class="zy_newsxq">
                  <div class="xq_tit bdb_d">
                    <h1>
                      {{article.title}}
                    </h1>
                    <p>
                      <span>作者：{{article.author}}</span
                      <span>发布时间：{{article.publishDate}}</span>
                    </p>
                  </div>
                  <div class="txt">
                     <p v-html="article.content"></p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            currpage: "list", //detail
            article: {},
            list: [],
            page: {},
        };
    },
    created() {
        let id = this.getUrl('id');
        if(id){
            this.toDetail(id)
        }
        else{
            this.loadData(1);
        }
    },
    methods: {
        loadData: function (pagenum) {
            axios
                .get("/api/index/notice/page?pageSize=5&pageNum=" + pagenum)
                .then((d) => {
                    if (d.data.code == 200) {
                      this.currpage= "list";
                        let result = d.data.data;
                        this.list = result.list;
                        this.page = d.data.data;
                    }
                });
        },
        toDetail: function (id) {
            axios.get("/api/index/notice/get?id=" + id).then((d) => {
                this.currpage = "detail";
                this.article = d.data.data;
            });
        },
        getUrl(name){
            var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
            var r = window.location.search.substr(1).match(reg)
            if (r !== null){ return r[2]; }
        }
    },
};
</script>



